@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.answers {
    display: flex;
    flex-direction: column;
    background-color: #F1F0EA;
    width: 40%;
    padding: 20px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 100px;
    justify-content: center;
    align-items: center;
}

h1, h5 {
    text-align: center;
}

h5 {
    font-family: 'Quicksand', sans-serif;
    font-size: 20px;
}

.Container {
    text-align: center;
    margin-bottom: 5px;
}

#submitBtn {
    cursor: pointer;
    font-size: 25px;
    position: relative;
    z-index: 0;
    border: none;
    outline: none;
    background-color: rgb(0, 0, 0);
    color: rgb(255, 255, 255);
    border-radius: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 90px;
    padding-right: 90px;
    margin-top: 20px;
    margin-left: 20px;
    margin-right: auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

#submitBtn:hover {
    color: rgb(0, 0, 0);
    background-color: rgb(255, 255, 255);
    border: 2px solid rgb(0, 0, 0);
}

#submitBtn:active {
    color: rgb(255, 255, 255);
    background-color: rgb(169, 169, 169);
    border: 2px solid rgb(255, 255, 255);
}
.form {
    display: flex;
    flex-direction: column;
    background-color: #F1F0EA;
    width: 20%;
    margin-top: 250px;
    margin-left: auto;
    margin-right: auto;
    padding: 10px;
    justify-content: left;
}

input[type="text"] {
    padding: 5px;
}

input[type="submit"][value="Go"] {
    cursor: pointer;
    font-size: 25px;
    position: relative;
    z-index: 0;
    border: none;
    outline: none;
    background-color: rgb(0, 0, 0);
    color: rgb(255, 255, 255);
    border-radius: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
}

input[type="submit"][value="Go"]:hover {
    color: rgb(0, 0, 0);
    background-color: rgb(255, 255, 255);
    border: 2px solid rgb(0, 0, 0);
}

input[type="submit"][value="Go"]:active {
    color: rgb(255, 255, 255);
    background-color: rgb(169, 169, 169);
    border: 2px solid rgb(255, 255, 255);
}
.start {
    position: absolute;
    display: flex;
    background-color: #F1F0EA;
    width: 50%;
    margin: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    height: 50%;
    border-radius: 30px;
    justify-content: center;
    align-items: center;
}

.glow-on-hover {
    font-size: 25px;
    width: 220px;
    height: 50px;
    border: none;
    outline: none;
    color: #fff;
    background: #111;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border-radius: 10px;
}

.glow-on-hover:before {
    content: '';
    background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
    position: absolute;
    top: -2px;
    left:-2px;
    background-size: 400%;
    z-index: -1;
    -webkit-filter: blur(5px);
            filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    -webkit-animation: glowing 20s linear infinite;
            animation: glowing 20s linear infinite;
    opacity: 0;
    -webkit-transition: opacity .3s ease-in-out;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
}
.glow-on-hover:active {
    color: #000
}
.glow-on-hover:active:after {
    background: transparent;
}
.glow-on-hover:hover:before {
    opacity: 1;
}
.glow-on-hover:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #111;
    left: 0;
    top: 0;
    border-radius: 10px;
}
@-webkit-keyframes glowing {
    0% { background-position: 0 0; }
    50% { background-position: 400% 0; }
    100% { background-position: 0 0; }
}
@keyframes glowing {
    0% { background-position: 0 0; }
    50% { background-position: 400% 0; }
    100% { background-position: 0 0; }
}
